import React from 'react';
import Team1Logo from '../TeamLogoImages/KTLogo.svg';
import Team2Logo from '../TeamLogoImages/KTLogo.svg';

function GameDetails({ team1Logo, team2Logo, result, players }) {
    return (
        <div className="game-details">
            <div className="team-logos">
                <img src={team1Logo} alt="Team 1 Logo" />
                <img src={team2Logo} alt="Team 2 Logo" />
            </div>
            <div className="game-result">{result}</div>
            <div className="player-list">
                <h3>주요 선수</h3>
                <ul>
                    {players.map((player, index) => (
                        <li key={index}>{player}</li>
                    ))}
                </ul>
            </div>
        </div>
    );
}

// App 컴포넌트
export default function App() {
    // 예시 데이터
    const game1 = {
        team1Logo: require('../TeamLogoImages/KTLogo.svg'), // TeamLogoImages 모듈에서 로고 이미지 가져오기
        team2Logo: require('../TeamLogoImages/KTLogo.svg'), // TeamLogoImages 모듈에서 로고 이미지 가져오기
        result: '경기 결과 1',
        players: ['선수1', '선수2', '선수3'],
    };

    const game2 = {
        team1Logo: require('../TeamLogoImages/KTLogo.svg'), // TeamLogoImages 모듈에서 로고 이미지 가져오기
        team2Logo: require('../TeamLogoImages/KTLogo.svg'), // TeamLogoImages 모듈에서 로고 이미지 가져오기
        result: '경기 결과 2',
        players: ['선수4', '선수5', '선수6'],
    };

    return (
        <div className="app">
            <h1>두 경기의 경기내역</h1>
            <GameDetails
                team1Logo={game1.team1Logo}
                team2Logo={game1.team2Logo}
                result={game1.result}
                players={game1.players}
            />
            <GameDetails
                team1Logo={game2.team1Logo}
                team2Logo={game2.team2Logo}
                result={game2.result}
                players={game2.players}
            />
        </div>
    );
}

